<template>
  <section class="index">
    <div class="first">
      指标
      <ul class="paper" >
        <li class="item">

          <div style="height: 40px;width: 50px;float: left;padding-bottom: 50px;">
            <i class="el-icon-s-data" style="font-size: 40px;" ></i> </div>
         <div style="float: left;padding-top: 20px;">班级数量
            <br><span style="font-size: 20px;">111</span>
          </div>
        </li>
        <li class="item">
          <div style="height: 40px;width: 50px;float: left;padding-bottom: 50px;">
            <i class="el-icon-school" style="font-size: 40px;" ></i> </div>
         <div style="float: left;padding-top: 20px;">年级数量
            <br><span style="font-size: 20px;">111</span>
          </div>
        </li>
        <li class="item">
          <div style="height: 40px;width: 50px;float: left;padding-bottom: 50px;">
            <i class="el-icon-user" style="font-size: 40px;" ></i> </div>
         <div style="float: left;padding-top: 20px;">学生数量
            <br><span style="font-size: 20px;">111</span>
          </div>
        </li>
        <li class="item">
          <div style="height: 40px;width: 50px;float: left;padding-bottom: 50px;">
            <i class="el-icon-s-custom" style="font-size: 40px;" ></i> </div>
         <div style="float: left;padding-top: 20px;">教师数量
            <br><span style="font-size: 20px;">111</span>
          </div>
        </li>
        <li class="item">
          <div style="height: 40px;width: 50px;float: left;padding-bottom: 50px;">
            <i class="el-icon-tickets" style="font-size: 40px;" ></i> </div>
         <div style="float: left;padding-top: 20px;">试卷数量
            <br><span style="font-size: 20px;">111</span>
          </div>
          
        </li>
      </ul>
    </div>
    <div class="second" style="float:left;width: 100%;">
    图表
    <div id="myChart" :style="{width: '600px', height: '300px'}"></div>
    </div>
    <div class="thrid" style="float:left">
公告 
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      like: true,
        value1: 4154.564,
        value2: 1314,
        title: "增长人数",
      user: { //用户信息
        userName: null,
        userId: null
      },
      
    }
  },
  created() {
    this.getUserInfo()
  },
  mounted(){
    this.drawLine()
  },
  methods: {
    getUserInfo() { //获取用户信息
      let userName = this.$jwtDecode(sessionStorage.getItem("token")).name
      let userId = this.$jwtDecode(sessionStorage.getItem("token")).id
      this.user.userName = userName
      this.user.userId = userId
    },
    openMsg() {
      this.$alert('根据《国务院办公厅关于2019年部分节假日安排的通知》精神，越城区行政服务中心将于4月5日（星期五）至4月7日（星期天）进行清明节放假调休，共3天，放假期间不受理业务。4月8日（星期一）开始正常上班受理业务。望市民朋友相互转告，给您带来不便，敬请谅解。','清明节放假通知',{
        confirmButtonText: '确定'
        
      })
    },
    drawLine(){
        // 基于准备好的dom，初始化echarts实例
        let myChart =this.$echarts.init(document.getElementById('myChart'))
        // 绘制图表
        myChart.setOption({
            title: { text: '图表的例子' },
            tooltip: {},
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }]
        });
    }
  }
}
</script>


<style lang="less" scoped>

.paper {
  h4 {
    cursor: pointer;
  }
}
.paper .item a {
  color: #000;
}
.paper .item {
  float: left;
  margin-left: 60px;
  // width: 100px;
  border-radius: 4px;
  padding: 12px 18px;
  border: 1px solid #eee;
  box-shadow: 0 0 4px 2px rgba(217,222,234,0.3);
  transition: all 0.6s ease;
}
.paper .item:hover {
  box-shadow: 0 0 4px 2px rgba(140, 193, 248, 0.45);
  transform: scale(1.03);
}
.paper * {
  margin: 20px 0;
}


.index {
  margin-left: 30px;
.hello {
    font-size: 20px;
    color: #726f70;
    .icon-xihuan {
      font-size: 30px;
      color: #dd6572;
    }
  }
  .like {
    cursor: pointer;
    font-size: 25px;
    display: inline-block;
  }
  .msg {
    .title {
      font-size: 16px;
      color: #000;
      margin-top: 20px;
      margin-left: 10px;
    }
    ul {
      display: flex;
      flex-direction: column;
      width: 200px;
      overflow: hidden;
    }
    li {
      margin-top: 10px;
      font-size: 14px;
      color: lightcoral;
      cursor: pointer;
      display: inline-block;
    }
  }
}
</style>




